.modal {
  visibility: hidden;
  position: fixed; /* Stay in place */
  z-index: 25; /* Sit on top */
  left: 25vw;
  top: 10%;
  width: 50vw;
  overflow: auto; /* Enable scroll if needed */
  background-color: aliceblue;
  margin: auto;
  min-height: 200px;
  padding: 16px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex: 1 1;
      flex: 1 1;
}
.modalShow {
  visibility: visible;
}

.modalHeader {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 0.2 1;
      flex: 0.2 1;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: end;
      justify-content: flex-end;
  padding-top: 8px;
}
button {
