  display: -ms-flexbox;
  display: flex;
  -ms-flex: 0.2 1;
      flex: 0.2 1;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.titleHeader {
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  -ms-flex: 0.2 1;
      flex: 0.2 1;
}
.titleInput {
  padding: 4px;
  font-size: 24px;
  width: 80%;
  border-radius: 4px;
  -ms-flex: 0.8 1;
      flex: 0.8 1;
}

.bodyDiv {
