.App {
  height: 100vh;
  position: relative;
  overflow: hidden;
  background: -webkit-gradient(linear, left top, left bottom, from(#141e30), to(#243b55));
  background: -webkit-linear-gradient(#141e30, #243b55);
  background: -o-linear-gradient(#141e30, #243b55);
  background: linear-gradient(#141e30, #243b55);
}

.Notesbg {
  height: 90%;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  background: -webkit-gradient(linear, left top, left bottom, from(#403b4a), to(#e7e9bb));
  background: -webkit-linear-gradient(#403b4a, #e7e9bb);
  background: -o-linear-gradient(#403b4a, #e7e9bb);
  background: linear-gradient(#403b4a, #e7e9bb);
  z-index: 2;
}
.createButton {
  position: absolute;
  background-color: #5cb85c;
  z-index: 5;
  margin-top: 4px;
  margin-left: 16px;
  height: 60px;
  display: block;
}



